.contact_header{
  background-color: aqua;
  height:5rem;
  border-radius: 25px;
  margin:1rem;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 300;
  text-align: center;
}
.contact_content{
  position: relative;
  top:1rem;
}
.contact_para{
  position: relative;
  bottom:.7rem;
}

.quote{
  text-align: center;
  width: 15rem;
  position: relative;
  left:1.8rem;
  top:1rem;
}

.contact_info{
  width:20rem;
  position: relative;
  left:1.2rem;
  top: 3rem;
}
.ducky_pic{
  height: 13rem;
  width: auto;
  z-index: 9;
  position: relative;
  bottom: 10rem;
  left: 4rem;
}
.ducky{
  margin-top: 1rem;
}
.contact_container{
  overflow: scroll;
  overflow-x: hidden;
  overflow-y: hidden;
}

@media screen and (min-width: 1024px){
  body{
    overflow-x: hidden;
  }
  .test_contact{
    color: brown;
          font-family: 'Roboto', sans-serif;
          font-weight: lighter;
          text-align: center;
          position: relative;
          left:3rem;
          top:4rem;
  }
  
  .stranger_text{
    font-weight: lighter;
  }
  
  .duck{
    position: relative;
    bottom: 22rem;
    left: 25rem;
    z-index: 10;
  }
  
  #ducky{
    height: 20rem;
    width: auto;
    position: relative;
    left: 17rem;
    top:5rem;
  }
  
  #pond{
    background-color:#1490e4;
    position: relative;
    bottom: 31rem;
    width:40rem;
    left: 30rem;
    height:20rem;
    z-index: -10;
    border-radius: 100%;
  }
  
  #contact_buttons{
    text-align: center;
  }
  .MuiTypography-root.connect_header{
    font-size: 1.3rem;
    margin: 1rem;
  }
  
  .MuiTypography-root.quote{
    text-align: center;
    font-size: 1rem;
  }
  .MuiPaper-root.paper{
    height: 2rem;
    width: auto;
  }
  .MuiPaper-root.ducky{
    width: 20rem;
    position: relative;
    top:1rem;
  }
}


